import i18n from 'i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import { initReactI18next } from 'react-i18next';

export const localeToLanguageMap = {
  //  ar: '',
  de: 'Deutsch',
  // el: '',
  en: 'English',
  // es: 'Espaol',
  // fil: 'Filipino',
  //  fr: 'Franais',
  //  hi: '',
  //  id: 'Bahasa Indonesia',
  //  it: 'Italiano',
  // ko: '',
  // ms: 'Bahasa Melayu',
  // nl: 'Nederlands',
  // 'pt-BR': 'Portugus do Brasil',
  // 'pt-PT': 'Portugus de Portugal',
  // ru: 'P',
  // se: 'Svenska',
  // tr: 'Trke',
  // uk: '',
  zh: '',
};

const o_risksNstrats = code => {
  try {
    return require(`./locales/${code}/risksNstrats.json`);
  } catch {
    return null;
  }
};
const resources = Object.fromEntries(
  Object.keys(localeToLanguageMap).map(code => [
    code,
    {
      translation: {
        ...require(`./locales/${code}/main.json`),
        ...o_risksNstrats(code),
      },
    },
  ])
);

i18n
  // load translation using http -> see /public/locales
  // detect user language
  // learn more: https://github.com/i18next/i18next-browser-languageDetector
  .use(LanguageDetector)
  // pass the i18n instance to react-i18next.
  .use(initReactI18next)
  // init i18next
  // for all options read: https://www.i18next.com/overview/configuration-options
  .init({
    resources,
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false, // not needed for react as it escapes by default
    },
  });

// eslint-disable-next-line no-restricted-syntax
export default i18n;
